<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full  d-flex">
                    <h4 v-if="AddTag" class="modal-title">{{ $t('AddTag') }}</h4>
                    <h4 v-if="UpdateTag" class="modal-title">{{ $t('UpdateTag') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    props: {
        TagId: {
            type: Object,
            required: true
        },
        Item: {
            type: Array,
            required: false
        }
    },
    data() {
        return {
            isLoading: false,
            AddTag: true,
            projectId: '',
            companyId: '',
            UpdateTag: false,
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                layoutType: "double",
                Data: [{
                    astype: "TextField",
                    label: this.$t('Tag Name'),
                    name: "TagName",
                    value: "",
                    placeholder: this.$t('Enter Tag Name'),
                    validationRules: {
                        "required": true
                    },
                    disabled: false,
                },
                {
                    astype: "SelectField",
                    label: this.$t('Status'),
                    name: "Status",
                    value: "",
                    placeholder: "",
                    validationRules: {
                        "required": false
                    },
                    config: {
                        options: [],
                    },
                    visibility: true
                }
                ]
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('TagDescription'),
                    name: "TagDescription",
                    value: "",
                    placeholder: this.$t('Enter Tag Description'),
                    validationRules: {
                        "required": false
                    },
                }]
            }
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                isVisible: true,
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.Close,
            },
            ],
            StatusDDL: [{
                label: 'Active',
                value: 1001
            },
            {
                label: 'Inactive',
                value: 1002
            }
            ]
        }
    },
        created() {
        const vm = this;
        vm.projectId = vm.$route.params.projectId;
        vm.BindStatus();

        debugger;

        if (vm.Item[0].TAG_ID==0 ) {
            vm.AddTag = true;
            vm.UpdateTag = false; 
                             
        }
        if ((vm.Item && vm.Item.length > 0)) {
            debugger
             vm.UpdateTag = true; 
               vm.AddTag = false; 
               const tagData = vm.Item[0]; 
                vm.FormSchema[0].Data[0].value = tagData.TAG_NAME || "";
                vm.FormSchema[0].Data[1].value = tagData.STATUS_ID?.toString() || "";
                vm.FormSchema[1].Data[0].value = tagData.TAG_DESC || "";
            }
    },  
    methods: {
        BindStatus() {
            var vm = this;
            vm.StatusDDL.forEach((itm) => {
                vm.FormSchema[0].Data[1].config.options.push({
                    name: itm.label,
                    value: `${itm.value}`
                })
            });
        },
           onSubmit() {
            debugger
            const vm = this;
            const tagIdFromItem = vm.Item && vm.Item.length > 0 ? vm.Item[0].TagId : 0;

            const objData = {
                tagId:  (this.Item && this.Item.length > 0 && this.Item[0].TAG_ID) ? this.Item[0].TAG_ID : 0,
                tagName: vm.FormSchema[0].Data[0].value,
                tagStatusId: vm.FormSchema[0].Data[1].value,
                tagDesc: vm.FormSchema[1].Data[0].value,
                moduleName: "Company",
            };

            const objectPostString = JSON.stringify(objData);
            debugger

            DataService.SaveTag(objectPostString)
                .then((response) => {
                    const res = response.data;

                    if (res && res.length > 0 && res[0].Status === "Success") {
                        const msg = vm.UpdateTag
                            ? vm.$t('UpdatedSuccess', [vm.$t('Tag')])
                            : vm.$t('AddedSuccess', [vm.$t('Tag')]);

                        vm.ShowAlert(msg, "success", true, vm.$t("Alert"));
                        vm.$parent.FetchData();
                        vm.Close();
                    } else if (res && res[0].Status === "Exist") {
                        vm.ShowAlert(vm.$t('TagAlreadyExists'), "warning", true, vm.$t("Alert"));
                    } else {
                        vm.ShowAlert(vm.$t('UnknownErrorOccur'), "failure", true, vm.$t("Alert"));
                    }
                })
                .catch(() => {
                    vm.ShowAlert(vm.$t('ServerError'), "failure", true, vm.$t("Alert"));
                });
        },
        Close() {
            this.$parent.ShowAddTag = false;
        }
    }


       
}
</script>