<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CompanyAdmin" SubModuleCode="CompanyAdmin"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TAG_ID"
            :HeaderText="$t('Tag')" :listType="['List']" :ListData="TagData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            :CurrentPageNumber="PageNumber" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
         <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <a class="inactive-disabled">
                        <span
                            v-bind:class="getStatusClass(data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive', 'STATUS_ID')">
                            {{ data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive' }}
                        </span>
                    </a>
                </template>
              
          </template>
        </tg-list>
        <AddTag v-if="ShowAddTag" :TagId="TagId" :Item="Item"></AddTag>
    </section>
</template>

<script>
import AddTag from './AddTag.vue';
import DataService from '../../../services/DataService';
import Schema from '../ManageTag/Schema';

export default {    
    components: {
        AddTag
    },
    data() {
        return {
            isLoading: false,
            ShowAddTag: false,
            TagData: [],
            Item:[],
            StatusListForListing: [],
            TotalRecord: 0,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
                StatusListForListing: [{
                STATUS: 'Active',
                STATUS_ID: 1001,
                STATUS_COLOR: "#ffffff",
                STATUS_BG_COLOR: "#28a745"
            },
            {
                STATUS: 'Inactive',
                STATUS_ID: 1002,
                STATUS_COLOR: "#ffffff",
                STATUS_BG_COLOR: "#d72435"
            }
            ],
            TotalPages: 0,
            TagId: 0,
            Headers: [{
                COLUMN_NAME: "TAG_NAME",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Tag Name",
                DISPLAY_ORDER: 1
            },
            {
                COLUMN_NAME: "TAG_DESC",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Description",
                DISPLAY_ORDER: 2
            },
            {
                COLUMN_NAME: "STATUS_ID",
                DATA_TYPE: "text",
                SORTABLE: true,
                VISIBLE: true,
                DISPLAY_NAME: "Status",
                DISPLAY_ORDER: 3,
                 settings: {
                    isInSlot: true
                 }
            }
            ]
        };
    },
    async created() {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        leftCommonSearch() { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        getStatusClass: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        sortdata: function (s) {
            
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddTag":
                    this.AddTag();
                    break;
                case "Active":
                    this.Active();
                    break;
                case "Inactive":
                    this.Inactive();
                    break;
                case "CommonDeleteData":
                    this.CommonDeleteData();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'Edit':
                    vm.Edit(item);
                    break;
            }
        },
        AddTag: function (item) {
            debugger
            var vm = this;
            vm.TagId = 0;
            vm.ShowAddTag = true;
        },
        Edit: function (item) {
            debugger
           var vm = this;
            vm.TagId=null,        
            vm.Item = [item];        
            vm.ShowAddTag = true;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];

            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchData: async function () {
            debugger
            var vm = this;
            vm.isLoading = true;
            var url = `tagName=&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&moduleName=`;
            await DataService.GetTagList(url).then(response => {
                debugger
                vm.TagData = [];
                if (response.data != '') {
                    vm.TagData = response.data;
                    vm.TotalRecord = response.data[0].TotalRecord;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.listheaderbuttons[1].isdisabled =true;
                    vm.listheaderbuttons[2].isdisabled =true;
                    vm.listheaderbuttons[3].isdisabled = true;
                }
            });
            vm.isLoading = false;
            
        },
        async changeStatus(ids, statusId) {
           debugger
            var vm = this;          

             {
                //
                vm.confirmR(vm.$t("Are you sure you want to change the status?"), true, (vm.$t("Update") + "  " + vm.$t("Group_Item")), function () {
                    var idsString = (typeof ids === 'string') ? ids : String(ids);
                    var idArray = idsString.includes(',') ? idsString.split(',').map(id => id.trim()) : [idsString.trim()];
                    idArray.forEach(id => {                       
                    const payload = {
                    ID: id,
                    StatusId: statusId,
                    IsFor: "talygen_tag",
                    Module: "CompanyAdmin",
                    Submodule: "CompanyAdmin"
                    };

                        DataService.CommonUpdateStatus(payload).then(response => {
                            debugger
                            if (response.data.status === 'Success') {
                                vm.ShowAlert(vm.$t('Tag has been successfully updated.'), "success", true, vm.$t("Alert"));                          
                           
                                vm.FetchData();
                            } else {
                                vm.ShowAlert(vm.$t('An error has occurred while updating Tag. Please retry.'), "error", true, vm.$t("Alert"));
                            }
                            vm.isLoading = false;
                        });
                    });
                });
               

            }
           


        },
        Active: function () {
            var vm = this;
            debugger
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            vm.changeStatus(val, 1001)
        },
        Inactive: function () {
            var vm = this;
            debugger
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            vm.changeStatus(val, 1002)
        },
        CommonDeleteData: function (event) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Project")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "Company",
                        subModuleCode: "Company",
                        refCode: 'Doc_Tag'
                    };
                    var objectPostString = JSON.stringify(postJSON)
                    DataService.CommonCommonDelete(objectPostString).then(function (response) {
                        vm.isLoading = false;
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Tag')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Tag')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },

    }
}
</script>